import { useTheme } from '../context/ThemeContext';
import { FaSun, FaMoon, FaGlobe } from 'react-icons/fa';

export default function ThemeControls() {
  const { theme, language, toggleTheme, toggleLanguage } = useTheme();

  return (
    <div className="fixed bottom-4 right-4 flex gap-4">
      <button
        onClick={toggleTheme}
        className="p-3 bg-black/20 backdrop-blur-sm rounded-full border border-mintGreen/20 shadow-lg text-mintGreen hover:text-mintGreenLight transition-colors duration-300"
      >
        {theme === 'dark' ? <FaSun size={24} /> : <FaMoon size={24} />}
      </button>
      <button
        onClick={toggleLanguage}
        className="p-3 bg-black/20 backdrop-blur-sm rounded-full border border-mintGreen/20 shadow-lg text-mintGreen hover:text-mintGreenLight transition-colors duration-300"
      >
        <FaGlobe size={24} />
        <span className="ml-2 text-sm">{language.toUpperCase()}</span>
      </button>
    </div>
  );
} 